/* Edited for Summit Theme */

.cart-summary-container {
  @extend .summary-container;
}

.cart-summary-title {
  @extend .summary-title;
}

.cart-summary-expander-container {
  padding: 0;
  clear: both;

  & .control-group {
    margin: 0;
  }
}
.cart-summary-expander-head {
  @extend .accordion-head;
}

.cart-summary-expander-head-toggle {
  @extend .accordion-head-toggle;

  /*font-weight: normal;
	padding-left: $sc-padding-lv2;
	padding-right: $sc-padding-lv2;*/
}

.cart-summary-expander-toggle-icon {
  @extend .accordion-toggle-icon;
}

.cart-summary-expander-toggle-icon-promocode {
  @extend .accordion-toggle-icon;
}

.cart-summary-expander-body {
  @extend .accordion-body-alt;
}

.cart-promocode-form-summary-button-apply-promocode {
  @extend .button-secondary;
  @extend .button-small;
  background-color: $sc-color-copy;
  border-color: $sc-color-copy;
}

.cart-summary-button-estimate {
  @extend .cart-promocode-form-summary-button-apply-promocode;
  margin-top: $sc-margin-lv4 / 2;
}

.cart-summary-estimate-input {
  width: 100%;
  margin-bottom: $sc-margin-lv2;
}

.cart-summary-button-container {
  border-top: $sc-theme-border-light;
  @extend .col-xs-12;
  @media (min-width: $screen-sm-min), (orientation: landscape) {
    padding: $sc-padding-lv5 0 $sc-padding-lv3 0;
    margin-bottom: $sc-margin-lv2;
    display: block;
  }
}

.cart-summary-button-proceed-checkout {
  display: none;
  @extend .button-proceed-checkout;

  &.cart-summary-button-proceed-checkout-sb {
    @extend .button-proceed-checkout;
    display: block;
  }

  @media (min-width: $screen-sm-min), (orientation: landscape) {
    display: block;
  }
}

.cart-summary-total {
  padding-bottom: $sc-padding-lv2;
  p {
    @extend .summary-total;
  }
}

.cart-summary-subtotal {
  @extend .summary-subtotal;
  /*margin-bottom:$sc-margin-lv3;
	font-weight:$sc-font-weight-bold;
	text-transform:uppercase;
	font-size:$sc-font-size-s;*/
}

.cart-summary-subtotal-legend {
  font-size: $sc-font-size-s;
  text-transform: capitalize;
  font-weight: $sc-font-weight-normal;
  margin-bottom: $sc-margin-lv3;
}

.cart-summary-grid {
  border-bottom: $sc-theme-border-light;
  clear: both;
}

.cart-summary-grid-float {
  @extend .summary-grid-float;
}

.cart-summary-shipping-cost-applied {
  margin-bottom: $sc-margin-lv4;
  font-size: $sc-font-size-m;
}

.cart-summary-grid-left {
  padding-left: $sc-padding-lv3;
}

.cart-summary-grid-right {
  float: right;
  padding-left: $sc-padding-lv1;
  /*padding-right:$sc-padding-lv3;*/
}

.cart-summary-remove-action {
  margin-left: $sc-margin-lv1;
  cursor: pointer;
  cursor: hand;

  i {
    @extend .icon-base;
    @extend %fa-times-circle;
    color: $sc-color-copy;
    position: relative;
    top: 2px;
  }
}

/* Summary extend labels */

.cart-summary-label-shipto {
  @extend .cart-summary-grid-left;
  margin: $sc-margin-lv2 0 $sc-margin-lv1;
  font-size: 14px;
  padding-left: 0;
}

.cart-summary-label-shipto-success {
  float: right;
}

.cart-summary-label-shipping {
  @extend .cart-summary-grid-left;
}

.cart-summary-amount-shipping {
  @extend .cart-summary-grid-right;
  color: $sc-color-copy;
}

.cart-summary-amount-handling {
  @extend .cart-summary-grid-right;
}

.cart-summary-label-tax {
  @extend .cart-summary-grid-left;
}

.cart-summary-amount-tax {
  @extend .cart-summary-grid-right;
  color: $sc-color-copy;
}

.cart-summary-label-total {
  @extend .cart-summary-grid-left;
}

.cart-summary-amount-total {
  @extend .cart-summary-grid-right;
}

.cart-summary-label-subtotal {
  @extend .cart-summary-grid-left;
}

.cart-summary-amount-subtotal {
  @extend .cart-summary-grid-right;
  color: $sc-color-primary;
}

.cart-summary-label-discount-total {
  @extend .cart-summary-grid-left;
  font-size: $sc-font-size-m;
}

.cart-summary-amount-discount-total {
  @extend .cart-summary-grid-right;
  font-size: $sc-font-size-m;
}

.cart-summary-label {
  display: block;
}

.cart-summary-input-country {
  margin-bottom: $sc-margin-lv2;
}

/* GIFT CERTIFICATES */
.cart-summary-giftcertificate-applied {
  padding-bottom: $sc-padding-lv2;
}

.cart-summary-giftcertificate-applied-title {
  @extend .cart-summary-grid-float;
}

.cart-summary-promocode {
  font-size: $sc-font-size-m;
  overflow: hidden;
}

.cart-summary-promocode-container {
  margin-top: $sc-margin-lv3;
}

.cart-summary-discount-applied {
}

.cart-summary-expander-tooltip {
  @extend .sc-tooltip;
}

/* FIX (rename/move)*/
.cart-summary-item-quantity-subtotal {
  font-weight: $sc-font-weight-normal;
}

.cart-summary-zip-code {
  @extend .input-small;
  //width: 100%;
}

.cart-summary-button-proceed-checkout {
  @extend .button-proceed-checkout;
}

.cart-summary-btn-paypal-express {
  @extend .btn-paypal-express;

  margin-left: -$sc-margin-lv3;
  margin-right: -$sc-margin-lv3;
  margin-bottom: $sc-margin-lv3;

  @media (min-width: $screen-sm-min), (orientation: landscape) {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
    padding-bottom: auto;
    padding-top: auto;
  }
}

.cart-summary-btn-paypal-express-image {
  width: 100%;

  @media (max-width: $screen-xs-max) {
    width: auto;
  }
}

.cart-summary-continue-shopping {
  @extend .small-title-navigable;
  display: inline-block;
  margin-top: $sc-margin-lv2;
}

.cart-summary-login-to-see-price {
  font-weight: normal;
}

.cart-summary-message {
  @extend .message;
}

.cart-summary-msg-description {
  @extend .message-info;
  display: block;
  text-align: left;
  margin-left: $sc-margin-lv2;
  margin-right: $sc-margin-lv2;
}

.cart-summary-pickup-container {
  margin-bottom: $sc-margin-lv4;
  font-size: $sc-font-size-m;
}

.cart-summary-pickup-label-free {
  @extend .cart-summary-grid-right;
  color: $sc-color-success;
}
