.transaction-line-views-selected-option-color {
  text-transform: uppercase;

  p {
    margin-bottom: $sc-margin-lv2;
  }
}
.transaction-line-views-selected-option-color-tiles-container {
  @extend .color-picker-xs;
  margin-bottom: $sc-margin-lv1;
  .items-views-selected-option-color-tile {
    span {
      cursor: default;
    }
  }

  .transaction-line-views-selected-option-color-tile-color {
    display: block;
  }
}

.transaction-line-views-selected-option-color-text {
  font-size: $sc-font-size-s;
}

.transaction-line-views-selected-option-color-label {
  font-size: $sc-font-size-m;
}

.transaction-line-views-selected-option-color-label-text {
  @extend .sc-label;
  text-transform: capitalize;
}
