// Font variable for secondary font applied to buttons, nav, form fields, prices, product info, header, breadcrumb
$sc-theme-secondary-font: Droid Sans; /* editable({"type": "string", "label": "Secondary font", "description": "Additional font family used for buttons, nav, form fields, prices, product info, header, breadcrumb. Do not add multiple fonts separated by commas."}) */

// Font variables for body element
$sc-body-font-family: $sc-font-family; /* editable({"type": "string", "label": "Font family"}) */
$sc-body-font-size: $sc-font-size-m; /* editable({"type": "string", "label": "Mobile size"}) */
$sc-body-font-size-desktop: $sc-font-size-m; /* editable({"type": "string", "label": "Desktop size"}) */
$sc-body-font-style: normal;
$sc-body-line-height: 1.6; /* editable({"type": "string", "label": "Line height"}) */
$sc-body-letter-spacing: 0;
$sc-body-direction: ltr;

// Font variables for paragraph elements. Font styles are inherit from body.
$sc-paragraph-margin-top: 0;
$sc-paragraph-margin-bottom: 0;

