/* Edited for Summit Theme */

.cart-detailed {
  @extend .cart;
  @extend .content-background;
  margin-bottom: $sc-margin-base * 10;

  .transaction-line-views-selected-option-color-label-text,
  .transaction-line-views-tax-label,
  .cart-item-summary-item-list-actionable-amount-label,
  .cart-item-summary-item-list-actionable-label-qty {
    color: $sc-color-copy-dark;
    font-weight: 600;
  }

  .transaction-line-views-selected-option {
    .transaction-line-views-selected-option-label {
      color: $sc-color-copy-dark;
      font-weight: 600;
    }
  }
}

.cart-detailed-view-header {
}

.cart-detailed-title {
  @extend .cart-title;
}

.cart-detailed-title-details-count {
  @extend .cart-title-details-count;
  @extend .middle-price;
}

.cart-detailed-confirm-message {
}

.cart-detailed-right {
  @extend .cart-right;
  margin-top: $sc-margin-lv4;
  @media (min-width: $screen-sm-min) {
    margin-top: $sc-margin-lv3;
  }
}

.cart-detailed-body {
  @extend .cart-body;
}

.cart-detailed-left {
  @extend .cart-left;
}

.cart-detailed-empty {
  @extend .cart-empty;
}

.cart-detailed-footer {
}

.cart-detailed-header {
}

.cart-detailed-savedforlater {
  @extend .cart-savedforlater;

  /*.product-list-details-later {
		border-top:1px solid $sc-color-theme-border-light;
		padding-top: $sc-padding-lv6;
		border-bottom: 0;
		margin: 0;
	}
	.product-list-details-later-button-saveforlater-pusher {
		margin-bottom: 30px;
		margin-top: 0;
	}*/
}

.cart-detailed-proceed-to-checkout-container {
  @extend .cart-proceed-to-checkout-container;
}

.cart-detailed-proceed-to-checkout {
  @extend .cart-proceed-to-checkout;
}

.sticky-button-container-clone.sticked {
  left: 0;
  margin-left: 0;
}

.cart-detailed-body-empty-info {
  @extend .cart-body-empty-info !optional;
}

.cart-detailed-body-empty {
}

.cart-detailed-body-empty-title {
  @extend .cart-body-empty-title !optional;
}

.product-list-details-later {
  border-top: 1px solid $sc-color-theme-border-light;
  padding-top: $sc-padding-lv6;
}

.cart-detailed-row-fluid {
  @extend .row;
}

.cart-detailed-recently-viewed {
  border-top: 1px solid $sc-color-theme-border-light;
  padding-top: $sc-padding-lv6;
}

.cart-detailed-related {
}

.cart-detailed-correlated {
}

.cart-detailed-body-info {
}

.cart-detailed-body-empty-info {
  @extend .cart-body-empty-info;
}

.cart-detailed-item-view-cell-actionable-table {
  @extend .transaction-line-views-cell-actionable-table;
  border-collapse: collapse;

  .transaction-line-views-cell-actionable-table-middle,
  .transaction-line-views-cell-actionable-table-first,
  .transaction-line-views-cell-actionable-table-last {
    padding-bottom: $sc-padding-lv6;

    @media (min-width: $screen-md-min) {
      padding-bottom: $sc-padding-lv6;
    }
  }

  td {
    padding-bottom: $sc-padding-lv6;
  }
  .quantity-pricing td {
    padding-bottom: $sc-padding-lv1;
  }

  .transaction-line-views-price-lead {
    color: $sc-color-primary;
  }

  .product-line-sku-container {
    margin-top: $sc-margin-lv6;
  }
}

.cart-detailed-table-row-with-border {
}

.cart-outside-footer {
  @extend .container;
}

.cart-detailed-item-free {
}
.cart-detailed-item-free-info {
  @extend .row;
  margin: 0;
}
.cart-detailed-item-free-info .global-views-message-info {
  @extend .col-xs-12;
  @extend .col-md-8;
  @extend .col-md-offset-4;
  @extend .col-lg-offset-3;
  @extend .col-lg-9;
}
