.product-details-quantity-add {
  @extend .quantity-input-button-add;
}

.product-details-quantity-remove {
  @extend .quantity-input-button-remove;
}

.product-details-quantity-value {
  @extend .quantity-input;
  width: 90px;
  min-width: 90px;
  text-align: right;
  display: inline-block;
  margin-left: $sc-margin-lv2;
  margin-right: $sc-margin-lv2;
  @media (min-width: $screen-md-min) {
    margin-left: 0;
    margin-right: 0;
  }
