// Color palette
//
// Here you will find the main user interface color palette for default Summit theme.
// Shades of green, blue-grey, white and grey are used for backgrounds, borders, buttons and text all along the application.
//
// Styleguide 1.1

// Primary Color
//
// $sc-color-primary - The Primary Color is used for primary actions and highlighted information.
// $sc-color-primary-dark - A darker version of the primary color for further highlighting.
//
// Markup:
// <div class="styleguide-color-primary">sc-color-primary</div>
// <div class="styleguide-color-primary-dark">sc-color-primary-dark</div>
//
// Styleguide 1.1.2
$sc-color-primary: #66cc99; /* editable({"type": "color", "label": "Highlight Color", "description": "Used mainly for 'call to action' elements such as 'Buy now' buttons an highlighted elements such as the Minicart Icon."}) */
$sc-color-primary-dark: #336633; /* editable({"type": "color", "label": "Highlight Partner Color", "description": "Used mainly for button hover states and border color on some elements."}) */

// Secondary Color
//
// $sc-color-secondary - The Secondary Color is used for secondary actions. Different shades of this color are used for theme backgrounds, borders and text.
//
// Markup:
// <div class="styleguide-color-secondary">sc-color-secondary</div>
//
// Styleguide 1.1.3
$sc-color-secondary: #4f88b0; /* editable({"type": "color", "label": "Alternate Color", "description": "Used mainly for secondary action elements such as 'Subscribe to newsletter' buttons."}) */

// Neutral shades
//
// The neutral shade palette is a scale from 1000 (dark) to 0 (light).
// This theme defines a couple of medium scale values. The developer might find useful to add more intermetiate colors within the scale that might be referenced to this scale.
//
// $sc-neutral-shade - All the neutral shade variables are calculated from $sc-neutral-shade variable. $sc-neutral-shade is equivalent to $sc-neutral-shade-500. Therefore $sc-neutral-shade variable names ending in values above 500 will be darker and those below 500 will be lighter.
// $sc-neutral-shade-700 - The darkest value on this scale is raraly used. It may be found on some texts or backgrounds.
// $sc-neutral-shade-500 - In occasions you will find this shade assigned to text but most of the times it will be the shade used for shadows.
// $sc-neutral-shade-300 - This light shade is used exclusively used in texts.
// $sc-neutral-shade-0 - The lightest value on this scale is largely used and may be usually found in texts, backgrounds and borders.
//
//
// Markup:
// <div class="styleguide-color-neutral-shade">sc-neutral-shade</div>
// <div class="styleguide-color-neutral-shade-700">sc-neutral-shade-700</div>
// <div class="styleguide-color-neutral-shade-500">sc-neutral-shade-500</div>
// <div class="styleguide-color-neutral-shade-300">sc-neutral-shade-300</div>
// <div class="styleguide-color-neutral-shade-0">sc-neutral-shade-0</div>
//
// Styleguide 1.1.4
$sc-neutral-shade: #222426; /* editable({"type": "color", "label": "Neutral Shade", "description": "This is the base neutral shade color. In the case of the 'out of the box' theme #4D5256. If only this color is changed the rest of the neutral shade variants will be derived from this one using the preset theme shading definition: 700, 500, 300, 0."}) */
$sc-neutral-shade-700: darken(
  $sc-neutral-shade,
  18
); /* editable({"type": "color", "label": "Neutral Shade 700"}) */
$sc-neutral-shade-500: $sc-neutral-shade; /* editable({"type": "color", "label": "Neutral Shade 500"}) */
$sc-neutral-shade-300: lighten(
  $sc-neutral-shade,
  20
); /* editable({"type": "color", "label": "Neutral Shade 300"}) */
$sc-neutral-shade-0: lighten(
  $sc-neutral-shade,
  100
); /* editable({"type": "color", "label": "Neutral Shade 0"}) */

// Theme Palette
//
// Just as the neutral shades palette, the Theme Palette is a scale from 1000 (dark) to 0 (light).
// This theme defines a couple of medium scale values. The developer might find useful to add more intermetiate colors within the scale that might be referenced to this scale.
//
// $sc-color-theme - All the theme variables are calculated from $sc-color-theme variable. $sc-color-theme is equivalent to $sc-color-theme-500. Therefore $sc-color-theme variable names ending in values above 500 will be darker and those below 500 will be lighter.
// $sc-color-theme-900 - Used mainly for headings
// $sc-color-theme-700 - new one
// $sc-color-theme-500 - Used mainly for general backgrounds
// $sc-color-theme-300 - Used mainly for borders
// $sc-color-theme-100 - Used mainly for borders
//
// Markup:
// <div class="styleguide-color-theme">sc-color-theme</div>
// <div class="styleguide-color-theme-900">sc-color-theme-900</div>
// <div class="styleguide-color-theme-700">sc-color-theme-700</div>
// <div class="styleguide-color-theme-500">sc-color-theme-500</div>
// <div class="styleguide-color-theme-300">sc-color-theme-300</div>
// <div class="styleguide-color-theme-100">sc-color-theme-100</div>
//
// Styleguide 1.1.5
$sc-color-theme: #6d7d81; /* editable({"type": "color", "label": "Theme Color", "description": "This is the base theme color. If only this color is changed the rest of the theme color variants will be derived from this one using the preset theme shading definition: 900, 700, 500, 300, 100."}) */
$sc-color-theme-900: #0b274e; /* editable({"type": "color", "label": "Shade 900"}) */
$sc-color-theme-700: $sc-color-theme; /* editable({"type": "color", "label": "Shade 700"}) */
$sc-color-theme-500: lighten(
  $sc-color-theme,
  35
); /* editable({"type": "color", "label": "Shade 500"}) */
$sc-color-theme-300: desaturate(
  lighten($sc-color-theme, 45),
  22
); /* editable({"type": "color", "label": "Shade 300"}) */
$sc-color-theme-100: desaturate(
  lighten($sc-color-theme, 23),
  3
); /* editable({"type": "color", "label": "Shade 100"}) */

$sc-color-theme-border: $sc-color-secondary; /* editable({"type": "color", "label": "Border"}) */
$sc-color-theme-border-light: #edf0f2; /* editable({"type": "color", "label": "Border light"}) */
$sc-color-theme-background: $sc-color-theme-border-light; /* editable({"type": "color", "label": "Background"}) */
$sc-color-theme-content-background: $sc-neutral-shade-0; /* editable({"type": "color", "label": "Content background", "description": "Main content background color and background color of product info shown on Recently Viewed Items, Merchandising Zones and Related Items."}) */
// Used extensively as shorthand in Summit
$sc-theme-border: 1px solid $sc-color-theme; // borders in footer
$sc-theme-border-light: 1px solid $sc-color-theme-border-light; // borders in main content area, under nav, in cart and checkout

// Messaging Palette
//
// The message box is defined by a colored text displayed over a colored background with an outline border.
// It might or may not include an icon in which case it should use the same color as the text.
// Possible message types are: Success, Warning, Error and Information.
// Each type is defined by a main color used for text. Background and border colors are calculated according to the main color.
//
// $sc-color-success, $sc-color-success-background, $sc-color-success-border - Used for success messages.
// $sc-color-warning, $sc-color-warning-background, $sc-color-warning-border - Used for warning messages.
// $sc-color-error, $sc-color-error-background, $sc-color-error-border - Used for error messages.
// $sc-color-info, $sc-color-info-background, $sc-color-info-border - Used for information messages.
//
// Markup:
// <div class="styleguide-color-success">sc-color-success</div>
// <div class="styleguide-color-success-border">sc-color-success-border</div>
// <div class="styleguide-color-success-background">sc-color-success-background</div>
// <div class="styleguide-color-warning">sc-color-warning</div>
// <div class="styleguide-color-warning-border">sc-color-warning-border</div>
// <div class="styleguide-color-warning-background">sc-color-warning-background</div>
// <div class="styleguide-color-error">sc-color-error</div>
// <div class="styleguide-color-error-border">sc-color-error-border</div>
// <div class="styleguide-color-error-background">sc-color-error-background</div>
// <div class="styleguide-color-info">sc-color-info</div>
// <div class="styleguide-color-info-border">sc-color-info-border</div>
// <div class="styleguide-color-info-background">sc-color-info-background</div>
//
// Styleguide 1.1.6
$sc-color-success: #097839;
$sc-color-success-background: desaturate(lighten($sc-color-success, 69), 20);
$sc-color-success-border: desaturate(lighten($sc-color-success, 61), 20);

$sc-color-warning: #946a00;
$sc-color-warning-background: desaturate(lighten($sc-color-warning, 65), 1);
$sc-color-warning-border: desaturate(lighten($sc-color-warning, 50), 20);

$sc-color-error: #c01a29;
$sc-color-error-background: lighten($sc-color-error, 54);
$sc-color-error-border: desaturate(lighten($sc-color-error, 45), 20);

$sc-color-info: #15607b;
$sc-color-info-background: desaturate(lighten($sc-color-info, 68), 22);
$sc-color-info-border: desaturate(lighten($sc-color-info, 60), 22);

// Links
//
// Links are used primarily as a navigational element. Links may also change what or how data is displayed (view more, show all). If the action taken by the user will change or manipulate data, use a button.
// Two different color variables are defined for this element
//
// $sc-color-link - The color the link is written with. This color should ensure they look clickable.
// $sc-color-link-hover - The color with which links are hilighted when hovered.
//
// Markup:
// <div class="styleguide-color-link">sc-color-link</div>
// <div class="styleguide-color-link-hover">sc-color-link-hover</div>
// <div class="styleguide-color-link-active">sc-color-link-active</div>
//
// Styleguide 1.1.7
$sc-color-link: $sc-color-primary; /* editable({"type": "color", "label": "Link"}) */
$sc-color-link-hover: #1a78ba; /* editable({"type": "color", "label": "Hover state"}) */
$sc-color-link-active: #0067b9; /* editable({"type": "color", "label": "Active state"}) */

// Copy
//
// The out of the box theme uses only 3 different color shades when it comes to rendering text.
// The shades available are:
//
// $sc-color-copy-dark - Used for almost every text on the site.
// $sc-color-copy - Mainly used for values such as SKU
// $sc-color-copy-light - Used for labels
//
// Markup:
//<div class="styleguide-color-copy-dark">sc-color-copy-dark</div>
//<div class="styleguide-color-copy">sc-color-copy</div>
//<div class="styleguide-color-copy-light">sc-color-copy-light</div>
//
// Styleguide 1.1.8
$sc-color-copy-dark: $sc-neutral-shade; /* editable({"type": "color", "label": "Dark"}) */
$sc-color-copy: #5a5a5a; /* editable({"type": "color", "label": "Text"}) */
$sc-color-copy-light: lighten(
  $sc-color-copy,
  35
); /* editable({"type": "color", "label": "Light"}) */

$color-balance-progress-bar: #2d9e6f;
